
import React from 'react';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';


import "../styles/style.css"

// Importe as imagens que voc deseja exibir no carrossel

import companyImage3 from '../assets/logo1.png';

const Company = () => {
  return (
    <div className="about-container">
      <Grid className='box' container spacing={1}>
        <Grid item xs={8} sm={4} className="about-image-container">
         
              <img src={companyImage3} alt="Company 3" />
            
        </Grid>
        <Grid item xs={8} sm={4} className="about-text-container">
        <Typography variant="h4" gutterBottom style={{ paddingTop: '100px' , textAlign: 'center', paddingRight: '100px'}}>
            Sobre Nossa Empresa
          </Typography>
          <Typography variant="body1" >
          "A Vidraaria Dalton, localizada na zona leste de So Paulo, atende essa regio e arredores por mais de 10 anos. 
           Com experincia e dedicao, somos referncia em servios de vidros na rea.
           Especializados em envidraamento de todos os tipos, oferecemos servios abrangentes, 
           incluindo montagem, renovao e manuteno de aqurios, assegurando ambientes seguros e atraentes para espcies aquticas
           Na Vidraaria Dalton, comprometemo-nos a fornecer produtos de alta qualidade e servios excepcionais. 
           Nossa equipe qualificada est pronta para transformar suas ideias em realidade, seja na criao de ambientes modernos e funcionais ou na manuteno de aqurios deslumbrantes.

          </Typography>
        </Grid>
      </Grid>
    </div>
  );
};

export default Company;



